const MasterOccupationEnter = ({occupation, setOccupation, step, setStep}) => {

    const activateHint = (value) => {
        setOccupation(value);
    }

    const click = async () =>{
        if(occupation != ""){
            setStep(step + 1);
        }
    }

    return(<div className="phases-wrapper">
        <div className="signUpMaster-annotation">
            <h1>  ?</h1>
            <a>         .</a>
        </div>
        <div className="signUpMaster-inputs">
            <input placeholder=" " value={occupation} onChange={e => setOccupation(e.target.value)} className="text-input-field"></input>
            <div className="button-hints">
                <button className="hint" onClick={() => activateHint(" ")}> </button>
                <button className="hint" onClick={() => activateHint("  ")}>  </button>
                <button className="hint" onClick={() => activateHint("")}></button>
                <button className="hint" onClick={() => activateHint(" ")}> </button>
                <button className="hint" onClick={() => activateHint("")}></button>
                <button className="hint" onClick={() => activateHint(" ")}> </button>
            </div>
        </div>
        <button onClick={click} className='continue-button'></button>
    </div>);
};

export default MasterOccupationEnter;